// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from "vue";
import App from "./App";
import router from "./router";

Vue.config.productionTip = false;
//导入轮播图插件
import VueAwesomeSwiper from "vue-awesome-swiper";
//使用轮播图插件
Vue.use(VueAwesomeSwiper);
//图片懒加载
import VueLazyload from "vue-lazyload";
Vue.use(VueLazyload, {
  preLoad: 1.3,
  error:
    "https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=2215070899,285017355&fm=27&gp=0.jpg",
  loading:
    "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1524765825753&di=81de584edc88a8a52f77ed222f9f458e&imgtype=0&src=http%3A%2F%2Fimgsrc.baidu.com%2Fforum%2Fw%3D580%2Fsign%3D000a2d5cd4c8a786be2a4a065708c9c7%2F2f9fc3fdfc039245f39d6ca98294a4c27d1e2539.jpg",
  attempt: 1
});
//在进入路由之前，每次都会执行此方法,全局钩子,也有拦截功能
router.beforeEach((to, from, next) => {
  document.title = to.meta.title;
  /*   if ((to.path = "/list")) {
    next({ path: "/add" });
  } */
  next();
});
require("swiper/dist/css/swiper.css");
/* eslint-disable no-new */
new Vue({
  el: "#app",
  router,
  components: { App },
  template: "<App/>"
});



